import React from 'react'
import { render } from 'react-dom'
import Ajuda from './components/Ajuda'
import PlayCards from './components/PlayCards'

/**
  Definici de constants
  * defaultPoints emmagatzema els punts mxims per defecte
  * defaultThrows emmagatzema les tirades mximes per defecte
  * defaultCards emmagatzema el nombre de cartes per defecte
  * minCards emmagatzema el nombre de cartes mnimes
  * maxCards emmagatzema el nombre de cartes mximes
*/
const defaultPoints = 30
const defaultThrows = 5
const defaultCards = 3
const minCards = 3
const maxCards = 5

const cards = [
  {
    style: 'yellow',
    points: 1,
  },
  {
    style: 'blue',
    points: 2,
  },
  {
    style: 'green',
    points: 3,
  },
  {
    style: 'red',
    points: -2,
  },
  {
    style: 'purple',
    points: 5,
  },
]

function convertNumberToCard(randomNumber) {
  return cards[Math.abs(randomNumber) % cards.length]
}

class App extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      numCards: defaultCards,
      maxPoints: defaultPoints,
      maxThrows: defaultThrows,
      currThrows: 0,
      currPoints: 0,
      mode: null,
      errors: [],
      thrownCards: [],
    }
  }

  changeNumCards(e) {
    const { value } = e.target
    this.setState({
      numCards: parseInt(value, 10),
    })
  }

  addPoints(thrownCards) {
    let addPoints = 0
    thrownCards.forEach((card) => {
      addPoints += card.points
    })
    this.setState({
      currPoints: this.state.currPoints + addPoints,
    })
  }

  throwCards(numCards) {
    const randomNumbers = []
    const thrownCards = []
    for (let i = 0; i < numCards; i += 1) {
      const random = Math.trunc(Math.random() * 1000)
      const card = convertNumberToCard(random)
      randomNumbers.push(random)
      thrownCards.push(card)
    }
    this.addPoints(thrownCards)
    this.setState({
      currThrows: this.state.currThrows + 1,
      thrownCards,
    })
    this.checkEndGame(thrownCards)
    // printThrowInfo(thrownCards, randomNumbers)
    // checkEndGame(thrownCards)
  }

  pressThrowCards(mode) {
    const { numCards } = this.state
    if (numCards >= minCards && numCards <= maxCards) {
      if (!this.state.mode) {
        this.setState({
          mode,
          errors: [],
        })
      }
      this.throwCards(numCards)
    } else {
      this.setState({
        errors: [
          ...this.state.errors,
          `El nmero de cartes ha de ser entre ${minCards} i ${maxCards}`,
        ],
      })
    }
  }

  checkEndGame(thrownCards) {
    if (this.state.mode === 'points' && this.state.currPoints >= defaultPoints) this.endGame()
    else if (this.state.mode === 'throws' && this.state.currThrows >= defaultThrows) this.endGame()
    else if (thrownCards.every((val, i, arr) => val === arr[0])) this.endGame()
  }

  endGame() {
    // el.innerHTML += `<h3>PARTIDA FINALITZADA AMB
    // ${currpoints} PUNTS I ${currthrows} TIRADES</h3>`;
    this.restartGame()
  }

  restartGame() {
    this.setState({
      mode: null,
      currPoints: 0,
      currThrows: 0,
    })
  }

  render() {
    return (
      <div>
        <h1>Joc de les cartes</h1>
        <Ajuda cards={cards} points={this.state.maxPoints} throws={this.state.maxThrows} />
        <p>
          <label htmlFor="numCartes" className="hideable">
            Nmero de cartes:
            <input
              type="number"
              id="numCartes"
              onChange={e => this.changeNumCards(e)}
              value={this.state.numCards}
              className="hideable"
              min={minCards}
              max={maxCards}
            />
          </label>
        </p>
        {
          this.state.errors.map(error => <p className="alert">{error}</p>)
        }
        <p>
          <button mode="punts" onClick={() => this.pressThrowCards('points')} className="hideable">
            Jugar fins a {this.state.maxPoints} punts
          </button>
          <button mode="tirades" onClick={() => this.pressThrowCards('throws')} className="hideable">
            Jugar fins a {this.state.maxThrows} tirades
          </button>
        </p>

        <PlayCards cards={this.state.thrownCards} />
        {
          this.state.thrownCards.length > 0 &&
          <div>
            <p>Els punts generats en aquesta jugada sn:
              {this.state.thrownCards.map(card => card.points).join(', ')}
            </p>
            <p>Nmero de tirades: {this.state.currThrows}</p>
            <p className="textPoints">PUNTUACI</p>
            <p>Punts tirada:
              {this.state.thrownCards.map(card => card.points).reduce((a, b) => a + b)}
            </p>
            <p>Punts totals: {this.state.currPoints}</p>
          </div>
        }
      </div>
    )
  }
}

render(<App />, document.getElementById('app'))



